import React from 'react'
import { useEffect, useState } from 'react'
import "./statewise.css"
const StateWise = () => {

    const [data, setData] = useState([]);
    const getCovidData = async () => {


        try {
            const res = await fetch('https://api.covid19india.org/data.json');
            const actualData = await res.json();
            console.log(actualData);
            setData(actualData.statewise);
        }
        catch (e) {
            console.log(e);
        }

    }

    useEffect(() => {
        getCovidData();

    }, []);
    return (
        <>

            <div className="container-fluid mt-5">
                <div className="main-heading">

                    <h1><center><span className="font-weight-bold">India - </span>Covid19 Dashboard</center></h1>

                </div>
                <div className="table-responsive">
                    <table className="table table-hover">
                        <thead className="thead-dark">
                            <tr>
                                <th>State</th>
                                <th>Confirmed</th>
                                <th>Recoverd</th>
                                <th>Deaths</th>
                                <th>Active</th>
                                <th>Updated</th>
                            </tr>

                        </thead>
                        <tbody>
                            {
                                data.map((curElement, index) => {
                                    return (
                                        <tr key={index}>
                                            <td>{curElement.state}</td>
                                            <td>{curElement.confirmed}</td>
                                            <td>{curElement.recovered}</td>
                                            <td>{curElement.deaths}</td>
                                            <td>{curElement.active}</td>
                                            <td>{curElement.lastupdatedtime}</td>
                                        </tr>
                                    )

                                })
                            }



                        </tbody>
                    </table>
                </div>
            </div>
        </>
    )
}
export default StateWise